import TabBar from "./TabBar";
import { listData } from "../../server/employee";

import MescrollVue from "mescroll.js/mescroll.vue";
import "mescroll.js/mescroll.min.css";
export default {
  name: "employee",
  components: { TabBar, MescrollVue },
  data() {
    return {
      docmHeight: document.documentElement.clientHeight,  //默认屏幕高度
      showHeight: document.documentElement.clientHeight,   //实时屏幕高度
      hideshow: true,  //显示或者隐藏footer
      class1: "class1",
      class2: "class2",
      mescroll: null, // mescroll实例对象
      mescrollDown: {//下拉刷新的配置
        callback: this.downCallBack,
        clearEmptyId: "main",
        isBoth: false,
        isBounce: true,
        inOffsetRate :1
      },
      mescrollUp: {
        // 上拉加载的配置.
        callback: this.getList,
        htmlNodata: '<p class="upwarp-nodata">到底啦~~~</p>',
        noMoreSize: 5,
        page: {
          num: 0,
          size: 50,
          total: 0
        },
        empty: {
          warpId: "main",
          icon: "./static/mescroll/mescroll-empty.png",
          tip: "暂无相关数据~" //提示
        },
        htmlLoading:'<p class="upwarp-progress mescroll-rotate"></p><p class="upwarp-tip">加载中..</p>'
      },
      dataList: [],
      imgFlag: false,
      flag: false,
      value: "",
      imgUrl: require("@/assets/img/Group.png"),
      removeUrl: require("@/assets/img/remove.png"),
      pages: {
        page: 1,
        size: 50
      },
      placeholder:'输入姓名或客户工号'
    };
  },
  created() {
    console.log(this,'thiss');
  },
  mounted() {
    // history.pushState(null, null, document.URL);
    // window.addEventListener('popstate', function () {
    //   history.pushState(null, null, document.URL);
    // })
    window.onresize = () => {
      return (() => {
        this.showHeight = document.body.clientHeight;
      })()
    };
  },
  // 监听input
  watch: {
    value: function (newval,oldval) {
      console.log(newval,'oldval');
      if (this.value == "") {
        this.placeholder='输入姓名或客户工号'
        this.handleSeach()
      }else{
        this.placeholder=''
      }
    },
    showHeight: function () {
      if (this.docmHeight > this.showHeight) {
        this.hideshow = false
      } else {
        this.hideshow = true
      }
    }
  },
  methods: {
    //清空input
    handleCommentFocus(){
      this.placeholder=''
    },
    goInfo(id){
      this.$router.push(`/employeeInfo/${id}`);
    },
    //搜索列表数据
    search() {
      this.handleSeach();
    },
    async handleSeach() {
      let searchList = [];
      const that = this;
      await listData({
        page: this.pages.page,
        limit: this.pages.size,
        searchStr: this.value
      }).then(e => {
        if (e.data.code == 200) {
          that.searchList = e.data.data.list;
          that.dataList = that.searchList;
        }
      });
    },
     //下拉刷新上拉加载更多初始化
    mescrollInit(mescroll) {
      this.mescroll = mescroll;
    },
    //上拉的回调
    downCallBack(mescroll) {
      setTimeout(function() {
        mescroll.endSuccess();
      }, 1500);
    },
    getList(page, mescroll) {
      const that = this;
      listData({
        page: page.num,
        limit: page.size,
        searchStr: this.value
      }).then(e => {
        if (e.data.code == 200) {
          var arr = e.data.data.list;
          let data = page.num == 1 ? [] : this.dataList;
          this.dataList = this.dataList.concat(arr);
          //  console.log(this.dataList);
          this.$nextTick(() => {
            this.mescroll.endSuccess(arr.length);
          });
        }
      });
    },
    //点击按钮入职/离职的显示和隐藏
    handleImg() {
      console.log(1);
      this.flag = !this.flag;
      this.imgFlag = !this.imgFlag;
    },
    //跳转到入职页面
    handlePushEnter() {
      localStorage.removeItem("from_where");
      localStorage.removeItem("from_id");

      localStorage.removeItem("employeeName");
      localStorage.removeItem("customerEmployeeNo");
      this.$router.push({ path: "/AddEmployee" });
    },
    //跳转到离职页面
    handleLive() {
      this.$router.push({ path: "/leaveEmployee/id" });
    }
  }
};



